import React,{useState,useEffect} from 'react';
import logo from './logo.svg';
import "./circle.css";
import $ from 'jquery'
import axios from "axios";
import _ from 'underscore';
import {useDispatch} from 'react-redux';
import {selected} from './optSlice';
function ComponenT(argument) {
    const dispatch=useDispatch();
	const [data,setData]=useState([]); 
useEffect(()=>{
axios.get('intersets.json')
.then((result)=>{result.data.map((item)=>setData(item))})
},[])

useEffect(()=>{

var tilesize = 110;
var gRows = Math.floor($(".circle-area").innerWidth()/tilesize);
var gCols = Math.floor($('.circle-area').innerHeight()/tilesize);
var vals = _.shuffle(data);
var xpos = _.shuffle(_.range(gRows));
var ypos = _.shuffle(_.range(gCols));

_.each(vals, function(d,i){
    var $newdiv = $('<div/>').addClass("btn-circle-medium");
    $newdiv.click((event)=>{
        dispatch(selected($(event.target).text()))
    }).appendTo('circle-area').html(d);
    $newdiv.css({
        'position':'absolute',
        'left':(xpos[i] * tilesize)+'px',
        'top':(ypos[i] * tilesize)+'px'
    }).appendTo( '.circle-area' ).html(d);  
});

},[data])

	// body...
	return(
		<div className="circle-area">

   		</div>
		)
}
export default ComponenT;