import {createSlice} from '@reduxjs/toolkit'

export const optSlice=createSlice({
	name:'opt',
	initialState:{
		value:[]
	},
	reducers:{
		selected:(state,action)=>{
			      var i=0;
			      state.value.forEach((item)=>{
			      	if(item!==action.payload)
			      		i++;
			      })
			      if (i==state.value.length) {
                      state.value.push(action.payload)
			      }
				},
		deleted:(state,action)=>{
			state.value=state.value.filter(element => element !== action.payload);
		}
	}
})
export const selector=(state)=>state.c.value;
export default optSlice.reducer;
export const {selected,deleted} =optSlice.actions; 