import React from 'react';
import "./App.css";
import ComponenT from './circle'
import {useSelector,useDispatch} from "react-redux";
import {selector,deleted} from "./optSlice";
import Chip from '@material-ui/core/Chip'
import $ from 'jquery'
import Icon from '@material-ui/core/Icon'
import  SVG from './svg/theme.svg'
function App() {
  
  const value=useSelector(selector);
  console.log(value)
   const dispatch=useDispatch();
  const handleDelete=(item)=>{
    dispatch(deleted(item))
  }
  return (
    <div className="App">
    <header className="header">
      <h3 className="logo">PRIX<span className="light-weight-font">LED</span></h3>
    </header>
    <section className="section">
    <p>
    Select Your <br/>Intersets
    </p>
    <div className="intersets">
    {
      value.map((item)=>
      <Chip label={item}
            color="primary"
            key={item}
            className="transition-slow-appear"
            onDelete={()=>handleDelete(item)}/>)
    }
    </div>
    <small>*Pick at least three(3).</small><br/>
    <button type="button" className="nxt-btn" onClick={()=>console.log("next-button")}>NEXT<i className="material-icons" style={{verticalAlign:"middle",paddingLeft:"5px"}}>arrow_right_alt</i></button>
    <button type="button" className="bck-btn" onClick={()=>console.log("back-button")}>Back</button>
    </section>
        <ComponenT/> 
    <div className="theme">
    <img src={SVG} style={{height:400,width:400}} className="theme-svg"/>
    </div>
 
    </div>
  );
}
export default App;